.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    /* overflow-x: hidden; */
    /* overflow-y: auto; */
    overflow: scroll;
  };

.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
}

.total-amount {
    float: right;
    font-weight: bold;
}

.total-amount-1 {
    font-weight: bold;
}

.actions {
    text-align: right;
    margin-top: 3px;
}

.button--alt {
    border-radius: 40rem;
    background: #5f240d;
    border-color: #8a2b06;
    color: white;
    padding: 10px;
    width: 90px;
    margin-right: 1rem;

}

.button{
    border-radius: 40rem;
    background: #5f240d;
    border-color: #8a2b06;
    color: white;
    padding: 10px;
    width: 90px;
    margin-right: 3px;
}

.button:hover,
.button--alt:hover {
    background: white;
    color: #5f240d;
}

::-webkit-scrollbar {
    width: 0;
}
